
.DefaultLayout {
    min-height: 100vh;
    background: black;
    color: white;
}

.page-content {
    padding: 0 25px 0 25px;
    justify-self: center;
    text-align: left;
    flex-grow: 1;
}


.site-header {
    padding-top: 10px;
    background-color: #00B0F0;
    color: black;
}

.site-footer {
    padding-top: 1vh;
    padding-bottom: 1vh;
    text-align: center;
}

.cahaba-logo {
    margin-left: 25px;
    font-family: "Agency FB Bold";
    flex-grow: 1;
    text-decoration: none;
}
.cahaba-logo-main {
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    font-size: 96px;
}
.cahaba-logo-main a {
    color: white;
    text-decoration: none;
}
.cahaba-logo-sub {
    padding: 0 0 0 0;
    margin: -15px 0 0 3px;
    font-size: 32px;
}
.cahaba-logo-sub a {
    color: black;
    text-decoration: none;
}




.menu-list {
    padding: 0px;
}

.menu-box {
    font-family: "Agency FB Bold", sans-serif;
    font-size: 32px;
    display: block;
    padding: 0px;
    margin-left: 10px;
    margin-right: 10px;
}

td, th {
    border: 1px solid white;
}

/* Responsive Resizing */
@media screen and (max-width: 600px) {
    .cahaba-logo {
        margin-left: 10px;
    }
    .cahaba-logo-main {
        font-size: 48px;
    }
    .cahaba-logo-sub {
        font-size: 16px;
        margin: -10px 0 0 3px;
    }
}

/* Sponsors */
.sponsor-header {
   font-size: 32px;
   color: black;
   font-family: "Agency FB Bold"; 
   margin: 0 25px 0 0;
}
.sponsor-bar {
    background-color: #00B0F0;
    margin: 20px 0px 0px 0px;
    padding: 10px 0px 10px 25px;
    max-height: 200px;
}
.sponsor-box {
    background-color: black; /* Needed for opacity animations */
    margin: 0 10px 0 0;
    padding: 0 0 0 0;
    display: flex;
    flex-direction: column;
}
.sponsor-image {
    border: 1px solid black;
    height: 75px;
    padding: 0px;
    display: flex;
}
@media screen and (max-width: 500px) {
    .sponsor-bar {
        max-height: 100px;
    }
    .sponsor-header {
        font-size: 16px;
    }
    .sponsor-image {
        height: 25px;
    }
}



/* Responsive Dropdown */
@media screen and (max-width: 400px) {
    .menu-container {
        display : none
    }
}
@media screen and (min-width: 401px) {
    .dropbtn {
        display : none
    }
    .dropdown {
        display : none
    }
}

.dropbtn {
    font-size: 24px;
    font-weight: bold;
    padding-right: 10px;
    cursor: pointer;
}

.dropdown-hidden {
    display : none;
}

.dropdown-box {
    display: block;
    border: 1px solid black;
    background-color: #00B0F0;
    color: black;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.dropdown-link {
    text-decoration: none;
}