.horizontal-flex-container {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: row;
}

.horizontal-flex-container-wrapped {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.vertical-flex-container {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: column;
}

.vertical-center-flex-container {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.vertical-end-flex-container {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}


.thin {
    margin: 0px;
    padding: 0px;
}
