@font-face {
    font-family: "Agency FB Bold";
    src: url(AGENCYB.TTF) format("truetype");
}

body {
  padding: 0;
  margin: 0;
  font-family: "Arial", "Calibri", "Sans Serif";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


h1,h2,h3,h4,h5,h6 {
    font-family: "Agency FB Bold";
}

.page-header {
    margin: 10px 0 10px 0;
    padding: 0 0 0 0;
    font-size: 40px;
    font-family: "Agency FB Bold";
}
.sub-header {
    font-size: 24px;
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    font-family: "Agency FB Bold";
}

.body-text {
    font-size: 16px;
}
.small-header {
    font-size: 16px;
}

.black-white-link {
    text-decoration: none;
    color: black;
}
.black-white-link:hover {
    text-decoration: none;
    color: white;
}


.header-with-rule {
    font-size: 32px;
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    font-weight: normal;
    font-family: "Arial", "Calibri", "sans-serif"
}
.rule {
    width: 300px;
}

@media screen and (max-width: 400px) {
    .header-with-rule {
        font-size: 24px;
    }
    .rule {
        width: 200px;
    }
}